const getinitialState = () => {
    if(window.localStorage.getItem('LTStore') == undefined){
        return  {
            date:'2000-01-01',
            gender:'male',
            countryCode:'ua'
        }  
      }else{
        console.log('getting from local')
        return JSON.parse(window.localStorage.getItem('LTStore')).inputs;
      } 
}
const inputsReducer = (state = getinitialState(), action)=>{
    switch(action.type){
        case 'inputs/dateSet':
            return {...state,date:action.payload}
        case 'inputs/countrySet':
            return {...state,countryCode:action.payload}
        case 'inputs/genderSet':
            return {...state,gender:action.payload}
        default:
            return state
    }
}

export default inputsReducer;