export default function language(lang = 'en',word = ''){
    let langData = {
        'en':{
            '':''
        },
        'ua':{
            '':'',
            'Life Timer':'ЖиттєТаймер',
            'Male':'Чоловік',
            'Female':'Жінка',
            'Birthday date':'Дата нарождення',
            'Date':'Дата',
            'Gender':'Стать',
            'Select Gender':'Обери стать',
            'Country':'Країна',
            'Select Country':'Обери країну',
            'year':'рік',
            'years':'років',
            'yearss':'роки',
            'Ukraine':'Україна',
            '':''
        }
    }
    if(typeof langData[lang] == 'undefined'){
        // console.warn(`No data for language: '${lang}'`)
        return word;
    }else{
        if(typeof langData[lang][word] == 'undefined'){
            // console.warn(`No translating for word: '${word}'`)
            return word;
        }else{
            return langData[lang][word];
        }
    }
}