import React, { useState } from 'react';
// import { useSelector, useDispatch } from 'react-redux'
import {Input, Dropdown} from 'semantic-ui-react'
import countryOptions from '../json/countryOptions'
import language from '../json/language';

function Inputs(props) {
  const inputsValues = props.store.getState().inputs
  const langNow = props.store.getState().lang.lang
  const genderOptions = ()=>{
    return [
    {key:'M',value: 'male', text: language(langNow,'Male')},
    {key:'W',value: 'female', text: language(langNow,'Female')},
  ]}
  const handleDateChange = (e,{value})=>{
    props.store.dispatch({type:"inputs/dateSet",payload:e.target.value})
  }
  const handleCountryChange = (e,{value})=>{
    props.store.dispatch({type:"inputs/countrySet",payload:value})
  }
  const handleGenderChange = (e,{value})=>{
    props.store.dispatch({type:"inputs/genderSet",payload:value})
  }
  return (
    <div className="Inputs">
        <div className="inputs-inner">
          <label>
            <p>{language(langNow,'Birthday date')}:</p>
              <Input
                type="date" 
                placeholder={language(langNow,'Date')}
                value={inputsValues.date} 
                onChange={handleDateChange}
              />
            </label>
            <p></p>
            <label>
              <p>{language(langNow,'Gender')}:</p>
              <Dropdown
                placeholder={language(langNow,'Select Gender')}
                fluid
                selection
                onChange={handleGenderChange}
                value = {inputsValues.gender}
                options={genderOptions()}
              />
            </label>
            <p></p> 
            <label>         
              <p>{language(langNow,'Country')}:</p>
              <Dropdown
              placeholder={language(langNow,'Select Country')}
              fluid
              search
              selection
              onChange={handleCountryChange}
              value = {inputsValues.countryCode}
              options={countryOptions(langNow,language)}
            />
          </label>
        </div>

    </div>
  );
}

export default Inputs;