import React from 'react';
function Footer(props) {
  const handleLangChange = (e)=>{
    let value = e.target.attributes[0].value;
    props.store.dispatch({type:"lang/langSet",payload:value})
  }
  return (
    <div className="Footer">
      <span className='part langBtns'>
        <span className='langBtn' onClick={handleLangChange}><span value="en">ENG</span></span>
        <span className='langBtn' onClick={handleLangChange}><span value="ua">УКР</span></span>
      </span>
      <span className='part'>© Ted Frost Dec 2021</span>
      <span className='part'></span>
    </div>
  );
}

export default Footer;