import { createStore,combineReducers } from "redux";
import inputsReducer from "./inputsReducer"

const getInitialLangState = ()=>{
  if(window.localStorage.getItem('LTStore') == undefined){
    return  {  
      lang:'ua'
    }
  }else{
    return JSON.parse(window.localStorage.getItem('LTStore')).lang;
  } 
}
const langReducer = (state = getInitialLangState(), action)=>{
  console.log(state)
  switch(action.type){
      case 'lang/langSet':
          return {...state,lang:action.payload}
      default:
          return state
  }
}

const rootReducer = combineReducers({
  inputs: inputsReducer,
  lang: langReducer
});

const store = createStore(rootReducer)
store.subscribe(() => {
  window.localStorage.setItem('LTStore',JSON.stringify(store.getState()))
}) 

export default store;